import React, { useState, useContext } from 'react';
import Message from '../components/Message';
import { Link } from 'react-router-dom';
import { Form, Button, Row, Col, ListGroup, Image, Card } from 'react-bootstrap';
import { CartContext } from '../context/CartContext';
import Steps from '../components/Steps';
import { customRounder } from '../utils/Formater';

export const CreateOrderPage = (props) => {
   const { state } = useContext(CartContext);
   const { cartItems, shippingAddress, total, itemCount } = state;
   const [errorMsg, setErrorMsg] = useState('');

   state.shippingPrice = total > 150 ? 0 : 85;
   state.taxPrice = customRounder(Number(0.15 * total));
   state.totalPayable = customRounder(Number(state.shippingPrice + state.taxPrice) + Number(total));

   return (
      <>
         <Steps step1 step2 step3 />
         <Row>
            <Col md={8}>
               <ListGroup variant='flush'>
                  <ListGroup.Item>
                     <h4>Payment Method</h4>
                     <Col md={5}>
                        <Form.Group controllId='paymentMethodId'>
                           {/* <Form.Label>Payment Method</Form.Label> */}
                           <Form.Control as='select' id='paymentMethod' variant='custom-select'>
                              <option value='paypal' selected>
                                 PayPal or Credit Card
                              </option>
                              <option value='stripe'>Stripe</option>
                              <option value='paystack'>Paystack</option>
                           </Form.Control>
                        </Form.Group>
                     </Col>
                  </ListGroup.Item>
                  <ListGroup.Item>
                     <h4>Shipping Address</h4>
                     <p>
                        <strong>Address:</strong>
                        {shippingAddress.address},{shippingAddress.city}
                        <br />
                        {shippingAddress.email}, {shippingAddress.country}
                     </p>
                  </ListGroup.Item>
                  <ListGroup.Item>
                     <h4>Order Items</h4>
                     {cartItems.length === 0 ? (
                        <Message>Your cart is empty</Message>
                     ) : (
                        <>
                           <ListGroup variant='flush'>
                              {cartItems.map((item, index) => (
                                 <ListGroup.Item key={index}>
                                    <Row>
                                       <Col md={1}>
                                          <Image src={item.image} alt={item.name} fluid rounded />
                                       </Col>
                                       <Col>
                                          <Link to={`/product/${item._id}`}>{item.name}</Link>
                                       </Col>
                                       <Col md={1}>
                                          {item.size}
                                          {'"'}
                                       </Col>
                                       <Col md={4}>
                                          {item.quantity} x ${item.price} = $
                                          {item.quantity * item.price}
                                       </Col>
                                    </Row>
                                 </ListGroup.Item>
                              ))}
                           </ListGroup>
                        </>
                     )}
                  </ListGroup.Item>
               </ListGroup>
            </Col>
            <Col md={4}>
               <Card>
                  <ListGroup variant='flush'>
                     <ListGroup.Item>
                        <h2>Order Summary</h2>
                     </ListGroup.Item>
                     <ListGroup.Item>
                        <Row>
                           <Col>Items({itemCount})</Col>
                           <Col>${total}</Col>
                        </Row>
                     </ListGroup.Item>
                     <ListGroup.Item>
                        <Row>
                           <Col>Shipping</Col>
                           <Col>${state.shippingPrice}</Col>
                        </Row>
                     </ListGroup.Item>
                     <ListGroup.Item>
                        <Row>
                           <Col>Tax</Col>
                           <Col>${state.taxPrice}</Col>
                        </Row>
                     </ListGroup.Item>
                     <ListGroup.Item>
                        <Row>
                           <Col>Total</Col>
                           <Col>${state.totalPayable}</Col>
                        </Row>
                     </ListGroup.Item>
                     {errorMsg && (
                        <ListGroup.Item>
                           <Message variant='danger'>{errorMsg}</Message>
                        </ListGroup.Item>
                     )}
                     <ListGroup.Item>
                        <Button type='button' className='btn-block' disabled={cartItems === 0}>
                           Create Order
                        </Button>
                     </ListGroup.Item>
                  </ListGroup>
               </Card>
            </Col>
         </Row>
      </>
   );
};
